.aside{position:fixed;top:100px;left:25px;width:120px;height:50rem;padding:2rem 0;opacity:1;transition:opacity 0.2s 0.5s ease}.aside--hidden{opacity:0;transition:opacity 0.2s ease}.aside__bar{width:2px;height:100%;background:#fff;opacity:0.3;position:absolute;top:10px;left:25px}.aside__list{height:100%;display:flex;flex-direction:column;justify-content:space-between}.aside__section{display:flex;justify-content:space-between;padding-left:30px;position:relative}.aside__section-index{position:absolute;left:30px;opacity:0.5}.aside__section-name{opacity:0}.aside__section--active .aside__section-index{opacity:1;left:-10px}.aside__section--active .aside__section-name{opacity:1}@media (max-width: 900px){.aside{display:none}}

.content{position:relative;width:100%;transition:transform 0.3s 0.1s ease, opacity 0.3s 0.1s ease;transform-origin:left}.content.moved{transition:transform 0.3s 0.1s ease, opacity 0.3s 0.1s ease;transform:translateX(-50%);opacity:0.5}@media (max-width: 450px){.content.moved{transform:translateX(-90%)}}

.header{color:#fff;margin:0 auto;display:flex;flex-direction:column;align-items:center;max-width:85%}.hero-wrapper{font-size:1.6rem;background-image:url(/portfolio/static/media/cosmonaut.bb026ce2.png);background-repeat:no-repeat;background-position:bottom 0 right 0px;background-size:31em;width:100%}.hero{min-height:45vmin;color:#fff;display:flex;width:85%;flex-direction:column;align-items:flex-start;justify-content:flex-end;margin:0 auto}.hero__title{font-size:4em;letter-spacing:0.05em;line-height:1;text-transform:uppercase;margin-bottom:6rem;width:60%}.hero__title span{display:block;font-size:0.4em;font-weight:400;text-transform:none;opacity:0.9}.hero__cta{padding:0.3rem 0.9rem;color:#fff;background:transparent;border:none;outline:none;position:relative;display:flex;align-items:center;justify-content:center;text-transform:uppercase;cursor:pointer;margin-bottom:10rem}.hero__cta img{height:24px}.hero__cta::before{content:'';position:absolute;top:0;left:0;height:100%;width:70%;transform:translateX(35%);background:#a34bed;z-index:-1;transition:-webkit-filter 0.2s ease;transition:filter 0.2s ease;transition:filter 0.2s ease, -webkit-filter 0.2s ease}.hero__cta:hover::before{-webkit-filter:saturate(2);filter:saturate(2)}.header__info{display:grid;grid-template-columns:1fr 1fr 1fr;width:85%}.header__info-item{padding-top:30px;margin-bottom:50px;opacity:0.2;position:relative;border-top:4px solid #333}.header__info-item::after{content:'';position:absolute;top:-4px;left:0;height:4px;width:15%;background:#a34bed;opacity:0}.header__info-item:hover{opacity:1}.header__info-item:hover::after{opacity:1}.header__info-header{letter-spacing:0.05em;text-transform:uppercase;margin-bottom:24px}.header__info-text{max-width:85%;line-height:1.4}@media (max-width: 1024px){.header{max-width:95%}.hero-wrapper{background-size:65vmin}.hero__title{font-size:9vmin}}@media (max-width: 900px){.header{max-width:100%}.hero-wrapper{background-size:60vmin}.hero__title{width:70%;font-size:8vmin}}@media (max-width: 700px){.hero__title{font-size:7vmin}.hero-wrapper{background-size:50vmin}}

.main{width:100%}

.nav{display:flex;justify-content:space-between;align-items:center;height:8rem;background:#0c0c0c;padding:2rem 0;position:-webkit-sticky;position:sticky;top:0;left:0;z-index:100}.nav__brand{display:flex;margin-left:3rem;align-items:center}.nav__logo{height:3rem}.nav__dev{display:inline-block;text-transform:uppercase;font-weight:bold;margin-left:2rem;letter-spacing:0.05em}.nav__burger{width:40px;height:30px;display:flex;flex-direction:column;align-items:flex-end;justify-content:space-between;background:transparent;padding:4px;outline:none;border:none;cursor:pointer;position:relative}.nav__burger:focus{box-shadow:0 0 0 3px #a34bed}.nav__burger-part{width:100%;height:2px;background:#fff;transition:opacity 0.2s 0.2s ease, transform 0.2s 0.2s ease}.nav__burger-part:nth-child(2){transition:opacity 0.2s 0.2s ease, transform 0.2s 0.2s ease;width:80%}.nav__burger--open .nav__burger-part:first-child{transform:translateY(10px) rotate(45deg)}.nav__burger--open .nav__burger-part:nth-child(2){opacity:0;transition:opacity 0.2s ease, transform 0.2s ease;transform:translateX(200%)}.nav__burger--open .nav__burger-part:last-child{transform:translateY(-10px) rotate(-45deg)}.nav__list{font-size:1.6rem;border-left:1px solid #a34bed;position:absolute;padding:20px 50px;right:0;top:100%;max-width:100%;list-style:none;opacity:1;transition:opacity 0.1s ease-in-out, transform 0.1s ease-in-out;transform:scaleY(0);transform-origin:top}.nav__list--active{opacity:1;transition:opacity 0.3s 0.1s ease-in-out, transform 0.3s 0.1s ease-in-out;transform:scaleY(1)}.nav__list-item{font-size:7vmin;text-transform:uppercase;letter-spacing:0.12em;cursor:pointer;display:block;padding:0.5em;position:relative}.nav__list-item::after{content:'';position:absolute;top:0;left:0;width:2em;height:100%;z-index:-1;background:#a34bed;transition:transform 0.1s linear;transform:scaleX(0);transform-origin:left}.nav__list-item:hover::after{transform:scaleX(1)}

.section{display:flex;flex-direction:column;align-items:center;text-align:center;min-height:100vh}.section__title{text-transform:uppercase;letter-spacing:0.12em;margin-bottom:64px}

.skills-type{width:100%;display:flex;flex-direction:column;align-items:center;margin-bottom:96px}.skills-type:last-of-type .skills__list{grid-template-columns:repeat(6, 1fr)}.skills-type__title{margin-bottom:16px;font-weight:400;opacity:0.7}.skills__list{list-style:none;display:grid;grid-template-columns:1fr 1fr 1fr;width:50%;grid-gap:32px 16px;gap:32px 16px}.skills__item{display:flex;flex-direction:column;align-items:center}.skills__icon{height:80px;margin-bottom:16px}.skills__name{text-transform:uppercase;font-weight:bold;letter-spacing:0.1em}

